import React from 'react';
import Container from '../components/container';
import { Input, Form } from '../components/uiComponent';

const Upload = () => {
  return (
    <Container flexDirection="column">
      <div>미리보기 이미지</div>
      <Form>
        <label>파일 선택</label>
        <Input type="file" />

        <label>그림 제목</label>
        <Input type="text" />

        <label>그림 설명</label>
        <Input type="text" />
        <textarea></textarea>
      </Form>
    </Container>
  );
};

export default Upload;
